/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

html {
  font-size: 62.5%; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* All focus */
a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none; }

html {
  font-size: 62.5%; }

button,
input,
textarea {
  color: #000;
  background: transparent;
  border: 0; }

* {
  box-sizing: border-box; }

#Timeline .year line, #Timeline .dot {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

body {
  font-family: "Avenir", sans-serif;
  scroll-behavior: smooth;
  background: #999; }

.App {
  display: -webkit-flex;
  display: flex; }

#Timeline {
  margin: 10px; }
  #Timeline:hover ~ .Pop {
    opacity: 1; }

#Timeline .year line {
  stroke: #888;
  stroke-width: 4;
  cursor: pointer; }
  #Timeline .year line:hover {
    stroke: #777;
    stroke-width: 12; }

#Timeline .dot {
  fill: #666;
  cursor: pointer; }

#Timeline .dot[data-field="0"] {
  fill: #ff9999; }

#Timeline .dot[data-field="1"] {
  fill: #fff799; }

#Timeline .dot[data-field="2"] {
  fill: #aaff99; }

#Timeline .dot[data-field="3"] {
  fill: #99ffe6; }

#Timeline .dot[data-field="4"] {
  fill: #99bbff; }

#Timeline .dot[data-field="5"] {
  fill: #d599ff; }

#Timeline .dot[data-field="6"] {
  fill: #ff99cc; }

.Filters input {
  border: 1px red solid; }

.Pop {
  background: #666;
  color: #ccc;
  border-radius: 5px;
  min-width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 14px;
  font-size: 1.4rem;
  position: fixed;
  pointer-events: none;
  opacity: 0;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease; }
  .Pop p {
    margin: 5px 10px; }
    .Pop p span {
      color: #fff;
      font-weight: bold; }

